@import "../../Static/shared.css";
@import "../../Static/Slides/slides.css";
@import "history_page.css";

body:before {
    filter: hue-rotate(138deg) brightness(.5) saturate(2.5);

    animation: hue-rotate 20s infinite linear alternate;
}

@keyframes hue-rotate {
    from {
        filter: hue-rotate(138deg) brightness(.5) saturate(2.5);
    }

    to {
        filter: hue-rotate(235deg) brightness(.5) saturate(2.5);
    }
}

#cyberpunk {
    max-width: 550px;
}

#cyberpunk ~ figcaption {
    max-width: 500px;
}

#ue_lumen {
    max-width: 600px;
}

#ue_lumen ~ figcaption {
    max-width: 550px;
}

#kentucky_rz {
    max-width: 550px;
}

#kentucky_rz ~ figcaption {
    max-width: 500px;
}